<template>
<div class="h100 dark gold padding-lr" id="recipeComponent">
  <h2 class="center"></h2><br/>

  <label> </label>
  <el-autocomplete :fetch-suggestions="querySearch" placeholder=""
  :trigger-on-focus="false" v-model="food" @select="searchDish">
  </el-autocomplete>
  <br/><br/>

  <el-dropdown type="primary">
    <el-button >
    <i class="el-icon-arrow-down el-icon--right"></i>
    </el-button>
    <el-dropdown-menu slot="dropdown">
      <el-dropdown-item v-for="d in dishes" :key=d.value>
        <div @click="chooseDish(d)">
          {{d.value}}
        </div>
      </el-dropdown-item>
    </el-dropdown-menu>
  </el-dropdown>
  <br/><br/>

  <el-card>
  <div slot="header">
    <span>: {{dishNow.value}}</span>
  </div>
  <div style="white-space:pre">
    : {{dishNow.material}}
  </div>
  <div style="white-space:pre">
    : {{dishNow.step}}
  </div>
  </el-card>
</div>
</template>

<script>
export default {
  name: "EGRecipe",
  data: function () {
      return {
        dishes:[
          {value:"",material:"",step:""},
          {value:"",material:"",step:""},
          {value:"",material:"",step:""},
          {value:"",material:"",step:""},
          {value:"",material:"",step:""},
          {value:"",material:"",step:""},
          {value:"",material:"",step:""},
          {value:"",material:"",step:""},
          {value:"",material:"",step:""},
          {value:"",material:"",step:""},
        ],
        dishNow:{},
        food:""
      }
    },
    methods:{
      chooseDish(dish){
        this.dishNow = dish;
      },
      searchDish(item){
        this.dishNow = item;
      },
      querySearch(queryString, cb) {
        var dishes = this.dishes;
        var results = queryString ? dishes.filter(this.createFilter(queryString)) : dishes;
        cb(results);
      },
      createFilter(queryString) {
        return (dish) => {
          let flag = (dish.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
          return flag;
        };
      }
    },
};
</script>

