<template>
  <div id="videoComponent">
    <div class="scroll-v" style="overflow-x: hidden">
      <el-row style="background-color: #2b2b2b">
        <br/>
        <!--  -->
        <el-col :span="18" class="v70 padding-lr">
          <video
            height="100%"
            width="100%"
            controls
            autoplay
            @drop="drop($event)"
            @dragover="allowDrop($event)"
          ></video>
        </el-col>

        <!--  -->
        <el-col :span="6" class="v70 border-left padding-lr">
          <h2 class="center"></h2>
          <p class="center">:</p>
          <el-collapse>
            <el-collapse-item :key="c.title" :title="c.title" v-for="c in comments">
              <div
                @dragstart="drag($event)"
                draggable="true"
                style="word-break: break-all"
              >
                {{ c.url }}
              </div>
            </el-collapse-item>
          </el-collapse>
        </el-col>
      </el-row>

      <!--  -->
      <footer class="space-v">
        <!--  -->
        <el-divider content-position="center">EGGroup</el-divider>
        <!--  -->
        <el-row class="text-center">
          <el-col :span="6">
            <ul>
              <li></li>
              <li></li>
              <li></li>
              <li></li>
            </ul>
          </el-col>
          <el-col :span="6">
            <ul>
              <li></li>
              <li></li>
              <li></li>
              <li></li>
            </ul>
          </el-col>
          <el-col :span="6">
            <ul>
              <li></li>
              <li></li>
              <li></li>
              <li></li>
            </ul>
          </el-col>
          <el-col :span="6">
            <ul>
              <li></li>
              <li></li>
              <li></li>
              <li></li>
            </ul>
          </el-col>
        </el-row>
        <!--  -->
        <el-divider></el-divider>
        <!--  -->
        <el-row class="text-center">
          <el-col :span="9">
            <span>IM 2020 00000000</span>
          </el-col>
          <el-col :span="2">
            <el-avatar icon="el-icon-trophy"> </el-avatar>
          </el-col>
          <el-col :span="2">
            <el-avatar icon="el-icon-trophy"> </el-avatar>
          </el-col>
          <el-col :span="2">
            <el-avatar icon="el-icon-trophy"> </el-avatar>
          </el-col>
          <el-col :span="9">
            <span></span>
            <span>&nbsp;&nbsp;&nbsp;</span>
            <span></span>
          </el-col>
        </el-row>
      </footer>
    </div>
  </div>
</template>

<script>
export default {
  name: "EGVideo",
  data: function () {
    return {
      comments: {
        Local: {
          url: require("../assets/video/niceday.mp4"),
          title: "Have a nice day",
        },
        Internet: {
          url:require("../assets/video/game.mp4"),
          title: "Game",
        },
      },
    };
  },
  methods: {
    allowDrop(ev) {
      ev.preventDefault();
    },
    drag(ev) {
      ev.dataTransfer.setData("Text", ev.target.innerText);
    },
    drop(ev) {
      ev.preventDefault();
      var data = ev.dataTransfer.getData("Text");
      ev.target.src = data;
    },
  },
};
</script>

